@import url('https://fonts.googleapis.com/css2?family=Macondo&family=Roboto&display=swap');

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  height: 100vh;
  background:#f4f4f4;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Roboto',sans-serif;
}

.container{
  background:black;
  color: #e9f0f4;
  width:20rem;
  padding:2rem;
  margin-top: 4rem;
  border-radius: 1rem;
  box-shadow: 0px 9px 15px -3px rgba(0,0,0,0.1);
}
.pantalla{
  height: 4rem;
  margin-bottom:4rem;
  
}
.botoncaja{
  display:grid;
  grid-template-columns:repeat(4,1fr);
  gap: .5rem;
}
.boton{
  background:#e9f0f4;
  height: 3.5rem;
  padding: 10px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: #242424;
  border: none;
  font-size:1.5rem;
}
.boton:hover{
  border: 2px dotted #242424;
}
.igual{
  background:#4bd086;
  grid-column: 3 / 5;
}

.operaciones{
  background:#f79505;
}